<template>
<section>
<div :class="{'right-content':isRightContent}">
    <!-- <div class="container-fluid mt-3 tab-style plaintabs">right-content hr_jobpost -->
    <div class="card mb-3 probx">
        <div class="card-body">
            <div class="col-md-12 float-left p-0">
                <div class="heading-border">
                    <h5 class="h5 mb-2"><span> Campaign Details </span></h5>
                </div>
                <div class="row">
                    <div class="col-md-6 col-lg-3">
                        <div class="form-group"> 
                            <label><strong> Campaign Name: </strong>
                                <span class="answer rightlabeltext ml-1">{{CampaignName}}</span>
                            </label> 
                            <select class="form-control" v-model="CampaignId" @change="GetAnotherCampaignData"> 
                                <option value=""> {{$t("Select") }} {{$t("Campaign")}} </option>
                                <option v-for="item in CampaignData" :value="item.value">{{ item.name }}</option>
                            </select>
                        </div>
                    </div><div class="col-md-6 col-lg-3">
                        <div class="form-group">
                            <label>
                                <strong> Campaign Owner: </strong> 
                                <span class="answer rightlabeltext ml-1"> {{CampaignOwner}} </span>
                            </label>
                        </div>
                    </div><div class="col-md-6 col-lg-3">
                        <div class="form-group">
                            <label><strong> Send Type: </strong><span class="answer rightlabeltext ml-1"> {{sendType}} </span></label>
                        </div>
                    </div>
                    <div class="col-md-6 col-lg-3">
                        <div class="form-group">
                            <label>
                                <strong> Schedule Date: </strong><span class="answer rightlabeltext ml-1">{{$options.filters.formatDate(ScheduleDate)}}</span>
                            </label>
                        </div>
                    </div><div class="col-md-6 col-lg-3">
                        <div class="form-group">
                            <label>
                                <strong> Status: </strong> 
                                <span class="answer rightlabeltext ml-1"> {{Status}} </span>
                            </label>
                        </div>
                    </div><div class="col-md-6 col-lg-3">
                        <div class="form-group">
                            <label>
                                <strong> Send Through: </strong>
                                <span class="answer rightlabeltext ml-1"> {{ScheduleType}} </span>
                            </label>
                        </div>
                    </div>
                </div>
                <div class="col-md-12 float-left p-0 mt-3">
                    <div class="heading-border">
                        <h5 class="h5 mb-2"><span> Summary </span></h5>
                    </div>
                </div> 
                <div class="pl-0 border-1">
                    <div class="form-check form-check-inline">
                        <label class="switch low">
                            <input class="form-check-input custom-control-input dynamic" v-on:change="setSlider(0)" type="checkbox" /> 
                            <span class="slider round">
                            </span>
                        </label> &nbsp;
                        <label class="form-check-label font-weight-normal">{{ $t('Showstatisticsinnumbers')}}</label>
                    </div>
                    <v-date-picker is-range class="timedate input-group col-md-3 float-right" v-model="startEndDate" @input="onDateChange">
                     <template v-slot="{ inputValue, inputEvents, togglePopover }">
                       <input class="form-control" :value="inputValue.start + ' - ' + inputValue.end" v-on="inputEvents" />
                         <div class="input-group-append" v-on:click="togglePopover">
                        <span class="input-group-text">
                          <em class="fa fa-calendar"></em>
                        </span>
                         </div>
                     </template>
                    </v-date-picker> 
                </div> &nbsp;
            </div> 
            <div class="card-body">
              <div class="listing col-12 p-0"> 
                  <div class="row">
                      <div class="col-sm-12 col-md-6 col-lg-12 stat_slides mb-2">
                          <div class="w-100 float-left p-4 d-flex border boxex">
                              <div class="float-left w-80"> 
                                  <span class="float-left w-100 font-weight-medium">Successfully Delivery</span>
                                  <span class="float-left w-100 value_ss">{{StatisticsData[0].DELIVERED_COUNT}}<span v-if="isPercentage">%</span></span>
                              </div>
                              <div class="float-left w-80">
                                  <span class="float-left w-100 font-weight-medium">Opened</span>
                                  <span class="float-left w-100 value_ss">{{StatisticsData[0].READ_COUNT}}<span v-if="isPercentage">%</span></span>
                              </div>
                              <div class="float-left w-80">
                                  <span class="float-left w-100 font-weight-medium">Clicked</span>
                                  <span class="float-left w-100 value_ss">{{StatisticsData[0].CLICKED_COUNT}}<span v-if="isPercentage">%</span></span>
                              </div>
                              <div class="float-left w-80">
                                  <span class="float-left w-100 font-weight-medium">Soft Bounced</span>
                                  <span class="float-left w-100 value_ss">{{StatisticsData[0].SOFT_BOUNCED_COUNT}}<span v-if="isPercentage">%</span></span>
                              </div>
                              <div class="float-left w-80">
                                  <span class="float-left w-100 font-weight-medium">Hard Bounced</span>
                                  <span class="float-left w-100 value_ss">{{StatisticsData[0].HARD_BOUNCED_COUNT}}<span v-if="isPercentage">%</span></span>
                              </div>
                              <div class="float-left w-80">
                                  <span class="float-left w-100 font-weight-medium">Spam</span>
                                  <span class="float-left w-100 value_ss">{{StatisticsData[0].SPAM_COUNT}}<span v-if="isPercentage">%</span></span>
                              </div>
                              <div class="float-left w-80">
                                  <span class="float-left w-100 font-weight-medium">Unsubscribed</span>
                                  <span class="float-left w-100 value_ss">{{StatisticsData[0].UNSUBSCRIBED_COUNT}}<span v-if="isPercentage">%</span></span>
                              </div>
                          </div>
                      </div>
                  </div> 
              </div> 
            </div>
            <div class="col-md-12 float-left p-4 text-center">               
                <!-- <img src="~/Content/images/please_wait.gif" /> -->
                <div id="chart">
                    <VueApexCharts type="area" height="350" :options="chartOptions" :series="series"></VueApexCharts>
                </div>
            </div>
             <div class="col-md-12 float-left p-0 mt-3">
                <div class="heading-border">
                <h5 class="h5 mb-2"><span> Details </span>
                    <small class="float-right">
                      <a checkprivilege="yes" aria-hidden="true" href="#">
                        <i title="Add" class="fa fa-download pr-1"></i> Export
                      </a>
                    </small>
                </h5>
                </div>
                <div class="row">
                    <div class="col-md-12 col-lg-12">                        
                        <div class="tab_container">
                            <div class="responsive-tabs-container accordion-xs accordion-sm">
                                <ul class="nav nav-tabs all-tabs responsive-tabs mt-2 responivecustomtab HideTab ui-tabs-nav ui-corner-all ui-helper-reset ui-helper-clearfix ui-widget-header" id="myTab" role="tablist">
                                    <li class="nav-item ui-tabs-tab ui-corner-top ui-state-default ui-tab ui-tabs-active ui-state-active" role="tab" tabindex="0" aria-controls="tabs-1" aria-labelledby="tab-Details" aria-selected="true" aria-expanded="true">
                                        <a class="nav-link active productCreateTab1 ui-tabs-anchor" href="#tabs-1" data-toggle="tab" title="" @click="onUserGuideChange('DELIVERED')"
                                            role="presentation" id="tab-Details" data-toggle-tooltip="tooltip" data-original-title="Create Product" tabindex="-1">Delivered
                                        </a>
                                    </li>
                                    <li class="nav-item ui-tabs-tab ui-corner-top ui-state-default ui-tab" role="tab" tabindex="-1" aria-controls="tabs-2" aria-labelledby="tab-timeline" aria-selected="false" aria-expanded="false">
                                        <a class="nav-link gen_icon productDeployTab2 ui-tabs-anchor" href="#tabs-2" title="" 
                                            @click="onUserGuideChange('READ')" data-toggle="tab" role="presentation" 
                                            id="tab-timeline" data-toggle-tooltip="tooltip" data-original-title="Deploy Product" tabindex="-1">Opened
                                        </a>
                                    </li>
                                    <li class="nav-item ui-tabs-tab ui-corner-top ui-state-default ui-tab" role="tab" tabindex="-1" aria-controls="tabs-3" aria-labelledby="tab-timeline" aria-selected="false" aria-expanded="false">
                                        <a class="nav-link gen_icon_1 ui-tabs-anchor" href="#tabs-3" title="" @click="onUserGuideChange('CLICKED')" 
                                            data-toggle="tab" role="presentation" id="tab-timeline" data-toggle-tooltip="tooltip" data-original-title="Related Information" tabindex="-1">Clicked
                                        </a>
                                    </li>
                                    <li class="nav-item ui-tabs-tab ui-corner-top ui-state-default ui-tab" role="tab" tabindex="-1" aria-controls="tabs-4" aria-labelledby="tab-timeline" aria-selected="false" aria-expanded="false">
                                        <a class="nav-link gen_icon_2 ui-tabs-anchor" href="#tabs-4" title="" @click="onUserGuideChange('Replied')" 
                                            data-toggle="tab" role="presentation" id="tab-timeline" data-toggle-tooltip="tooltip" data-original-title="Attachments" 
                                            tabindex="-1">Replied 
                                        </a>
                                    </li>
                                    <li class="nav-item ui-tabs-tab ui-corner-top ui-state-default ui-tab" role="tab" tabindex="-1" aria-controls="tabs-4" aria-labelledby="tab-timeline" aria-selected="false" aria-expanded="false">
                                        <a class="nav-link gen_icon_2 ui-tabs-anchor" href="#tabs-5" title="" @click="onUserGuideChange('HARD_BOUNCE')"
                                            data-toggle="tab" role="presentation" id="tab-timeline" data-toggle-tooltip="tooltip" data-original-title="Attachments"
                                            tabindex="-1">Hard Bounced 
                                        </a>
                                    </li>
                                    <li class="nav-item ui-tabs-tab ui-corner-top ui-state-default ui-tab" role="tab" tabindex="-1" aria-controls="tabs-4" aria-labelledby="tab-timeline" aria-selected="false" aria-expanded="false">
                                        <a class="nav-link gen_icon_2 ui-tabs-anchor" href="#tabs-6" title="" @click="onUserGuideChange('SOFT_BOUNCE')"
                                            data-toggle="tab" role="presentation" id="tab-timeline" data-toggle-tooltip="tooltip" data-original-title="Attachments" 
                                            tabindex="-1">Soft Bounced 
                                        </a>
                                    </li>
                                    <li class="nav-item ui-tabs-tab ui-corner-top ui-state-default ui-tab" role="tab" tabindex="-1" aria-controls="tabs-4" aria-labelledby="tab-timeline" aria-selected="false" aria-expanded="false">
                                        <a class="nav-link gen_icon_2 ui-tabs-anchor" href="#tabs-7" title="" @click="onUserGuideChange('Complained')"
                                            data-toggle="tab" role="presentation" id="tab-timeline" data-toggle-tooltip="tooltip" data-original-title="Attachments" 
                                            tabindex="-1">Complained 
                                        </a>
                                    </li>
                                    <li class="nav-item ui-tabs-tab ui-corner-top ui-state-default ui-tab" role="tab" tabindex="-1" aria-controls="tabs-4" aria-labelledby="tab-timeline" aria-selected="false" aria-expanded="false">
                                        <a class="nav-link gen_icon_2 ui-tabs-anchor" href="#tabs-8" title="" @click="onUserGuideChange('Unsubscribed')"
                                            data-toggle="tab" role="presentation" id="tab-timeline" data-toggle-tooltip="tooltip" data-original-title="Attachments" 
                                            tabindex="-1">Unsubscribed
                                        </a>
                                    </li>
                                    <li class="nav-item ui-tabs-tab ui-corner-top ui-state-default ui-tab" role="tab" tabindex="-1" aria-controls="tabs-4" aria-labelledby="tab-timeline" aria-selected="false" aria-expanded="false">
                                        <a class="nav-link gen_icon_2 ui-tabs-anchor" href="#tabs-9" title="" @click="onUserGuideChange('Error')"
                                            data-toggle="tab" role="presentation" id="tab-timeline" data-toggle-tooltip="tooltip" data-original-title="Attachments" 
                                            tabindex="-1">Error 
                                        </a>
                                    </li>
                                </ul> 
                            </div>
                        </div>
                    </div>
                </div> 
                <tg-list 
                    :IsShowAction="false" ModuleName="CRM" SubModuleCode="" :renderRowActionMethod="renderActions" :showCheckBox="true" :SortExp="SortExp" 
                    IdentityColumn="" :HeaderText="$t('')" :listType="['List']" v-on:PagerButtonClick="pagerMethod" :ListDataCallBackFunction="FetchData" 
                    :ListData="StatisticsHeaderData" :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecord" :SearchFields="leftSearchFields"                 
                    :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" :CurrentPageNumber="PageNumber" v-on:SortdataButtonClick="sortdata">                
                </tg-list>
            </div> 
        </div>
    </div>
</div>
</section>
</template>

<script>
import VueApexCharts from "vue-apexcharts";
import CampaignDataServices from "../../../services/DataService";
export default {
    components: {
        VueApexCharts
    },
    props: {
        CampaignId:  {
            type: Number,
            required: true
        },
        isRightContent: {
            type: Boolean,
            required: true
        },
        LeftViewData: {
            type: String,
            required: true
        }
    },
    data: function () {
        return {
            sendType:'',
            startEndDate: {
                start: new Date(),
                end: new Date(),
            },
            CheckPercentageOrNumber:0,
            StatisticsHeaderData:[],
            StatisticsData:[{
                DELIVERED_COUNT:0,
                READ_COUNT:0,
                CLICKED_COUNT:0,
                SOFT_BOUNCED_COUNT:0,
                HARD_BOUNCED_COUNT:0
            }],
            series:[],
            chartOptions: {},
            CampaignData:[],
            isLoading: false,
            listheaderbuttons: '',
            TotalRecord: 0,
            SortExp: '',
            SortBy: '',
            PageNumber: 1,
            TotalPages: 0,
            CurrentPage: 1,
            isPercentage:true,
            PageSize: 10,
            MailChimpId:'',
            MailChimpDataObj:[],
            CampaignName:'',
            CampaignOwner:'',
            Status:'',
            ScheduleType:'',
            ScheduleDate:null,
            EmailDelivertStatus: 'DELIVERED',
            Headers: [{
                    COLUMN_NAME: "FULL_NAME",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Full Name",
                    DISPLAY_ORDER: 1
                },
                {
                    COLUMN_NAME: "EMAIL_TO",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Email",
                    DISPLAY_ORDER: 2
                },   
                {
                    COLUMN_NAME: "STATUS_NAME",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Status",
                    DISPLAY_ORDER: 3
                },
                {
                    COLUMN_NAME: "CREATED_AT",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Last Updated",
                    DISPLAY_ORDER: 4
                },
            ]
        }
    },
    created(){
        this.getCampaigns();
        this.FetchData();    
        this.onDateChange(); 
        this.getCampaignStatisticsDetail();
    },
    methods: {
        onDateChange:function() {
            var vm = this;
            vm.CheckPercentageOrNumber=0;
            vm.dateform =  new Date();
            vm.dateto =  new Date();
            if(vm.CampaignId!=undefined){
                var campaign_id=vm.CampaignId;
            }else{
                var campaign_id=vm.$route.params.id;
            }
            var url = `campaign_id=${campaign_id}&dateform=${vm.$options.filters.formatDate(vm.startEndDate.start)}&dateto=${vm.$options.filters.formatDate(vm.startEndDate.end)}`;
            CampaignDataServices.GetCampaignStatistics(url).then(response => {
                vm.series = [];
                vm.chartOptions = [];
                var getDates = vm.GetDateDifference();
                vm.series = [{
                    name: 'SUCCESSFULLY DELIVERY',
                    data: []
                },
                {
                    name: 'OPENED',
                    data: []
                },
                {
                    name: 'CLICKED',
                    data: []
                },
                {
                    name: 'SOFT BOUNCED',
                    data: []
                },
                {
                    name: 'HARD BOUNCED',
                    data: []
                },
                {
                    name: 'SPAM',
                    data: []
                },
                {
                    name: 'UNSUBSCRIBED',
                    data: []
                }]
                if(response.data != null){
                    vm.StatisticsData=response.data;
                    if(response.data[0].SCHEDULE_TYPE=='MailChimp'){
                        vm.MailChimpId=response.data[0].MAIL_CHIMP_ID;
                        this.getMailChimpData();
                    }
                    else{
                        vm.series[0].data.push(vm.StatisticsData[0].DELIVERED_COUNT);
                        vm.series[1].data.push(vm.StatisticsData[0].READ_COUNT);
                        vm.series[2].data.push(vm.StatisticsData[0].CLICKED_COUNT);
                        vm.series[3].data.push(vm.StatisticsData[0].SOFT_BOUNCED_COUNT);
                        vm.series[4].data.push(vm.StatisticsData[0].HARD_BOUNCED_COUNT);
                        vm.series[5].data.push(vm.StatisticsData[0].SPAM_COUNT);
                        vm.series[6].data.push(vm.StatisticsData[0].UNSUBSCRIBED_COUNT);
                    }
                }
                vm.chartOptions = {
                    toolbar: 
                    {
                        show: false
                    },
                    xaxis: {
                        categories: getDates,
                        title: {
                            text: ''
                        }
                    },
                    yaxis: {
                        title: {
                            text: ''
                        },
                    },
                    dataLabels: {
                        enabled: true,
                    },
                    stroke: {
                        curve: 'smooth'
                    },
                }
            })
        },
        getCampaignStatisticsDetail:function(){
            var vm=this;
            var url = `campaign_id=${vm.CampaignId}`;
            CampaignDataServices.CampaignStatisticsDetail(url).then(response => {
                vm.CampaignName=response.data[0].CampaignName
                vm.ScheduleDate=response.data[0].ScheduleDate
                vm.CampaignOwner=response.data[0].CampaignOwner
                vm.Status=response.data[0].StatusName
                vm.ScheduleType=response.data[0].CampaignTypeName
                vm.sendType=response.data[0].SendType;
            })
        },
        GetDateDifference(){
            var vm = this;
            if(vm.startEndDate!=undefined){
                const date = new Date(vm.dateform.getTime());
                const dates = [];
                const getFinalDates = [];
                while (date <= vm.dateto) {
                    dates.push(new Date(date));
                    date.setDate(date.getDate() + 1);
                }
                dates.forEach(item=>{
                    getFinalDates.push(vm.$options.filters.formatDate(item));
                })
                return getFinalDates;
            }
        },
        getCampaigns:function(){
            var vm = this;
            var url = "id=&moduleName=CRM&type=EMAIL_CAMPAIGNS";
            CampaignDataServices.getDDLData(url + "&search=").then(response => {
                vm.CampaignData=[];
                if(response.data !=''){
                    vm.CampaignData=response.data.DATA;
                }
            });
        },
        GetAnotherCampaignData:function(){
            var vm=this;
            vm.onDateChange();
            vm.getCampaignStatisticsDetail();
        },
        FetchData: function(){
            var vm = this;
            vm.isLoading = true; 
            var url =`campaignId=${vm.CampaignId}&dateform=&dateto=&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&status=&emailDeliveryStatus=${vm.EmailDelivertStatus}`;
            //var url = '';
            //var url =`campaignId=${vm.CampaignId}&dateform=${vm.$options.filters.formatDate(vm.startEndDate.start)}&dateto=${vm.$options.filters.formatDate(vm.startEndDate.end)}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&status=${vm.Status}&emailDeliveryStatus=${vm.EmailDelivertStatus}`;
            CampaignDataServices.GetEmailStatisticsDetails(url).then(response =>{
                vm.StatisticsHeaderData=[];
                if(response.data.data != undefined && response.data.data.length > 0){
                    vm.StatisticsHeaderData =  response.data.data;
                    vm.TotalRecord = response.data.data[0].TOTAL;
                    vm.TotalPages = Math.ceil(vm.TotalRecord / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                    vm.noRecord = false;
                }else{
                    vm.noRecord = true;
                }                
            });
            setTimeout(function () {
                vm.ResponsiveDataTable('tablelistingdata')
            }, 500);
            vm.isLoading = false;  
        },
        renderActions: function () {
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        sortdata: function (s) {
          var SortDir = "sort tb_headerSortDown";
           this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else
                {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s+'  span:first-child').attr("class", SortDir);
        },      
        onUserGuideChange: function(type){
            var vm = this;
            vm.EmailDelivertStatus=type;
            vm.FetchData();
        },
        setSlider:async function(){
            var vm = this;
            vm.StatisticsData=[];
            if(vm.CheckPercentageOrNumber==1){
                vm.StatisticsData.push(vm.MailChimpDataObj);
                vm.CheckPercentageOrNumber=0;
                vm.isPercentage=false;
            }
            else{
                vm.getMailChimpData();
            }
        },
        getMailChimpData:async function(id){
            var vm=this;
            vm.StatisticsData=[];
            var getDates = vm.GetDateDifference();
            var url = `campaignId=${vm.MailChimpId}&startDate=${vm.$options.filters.formatDate(vm.startEndDate.start)}&endDate=${vm.$options.filters.formatDate(vm.startEndDate.end)}`;
            await CampaignDataServices.GetCampaignStats(url).then(response => {
                var mailChimpDataObj=JSON.parse(response.data.mailChimpData);
                vm.MailChimpDataObj=mailChimpDataObj;
                if(vm.CheckPercentageOrNumber==0){
                    var mailChimpDataPercentageObj=JSON.parse(response.data.mailchimpDataPercentage);
                    var obj={
                        DELIVERED_COUNT:mailChimpDataPercentageObj.SUCCESSFULL_DELIVERED_PERCENTAGE.toFixed(2),
                        READ_COUNT:mailChimpDataPercentageObj.READ_PERCENTAGE.toFixed(2),
                        CLICKED_COUNT:mailChimpDataPercentageObj.CLICKED_PERCENTAGE.toFixed(2),
                        UNSUBSCRIBED_COUNT:mailChimpDataPercentageObj.UNSUBSCRIBED_PERCENTAGE.toFixed(2),
                        HARD_BOUNCED_COUNT:mailChimpDataPercentageObj.HARD_BOUNCED_PERCENTAGE.toFixed(2),
                        SOFT_BOUNCED_COUNT:mailChimpDataPercentageObj.SOFT_BOUNCED_PERCENTAGE.toFixed(2)
                    }
                    vm.StatisticsData.push(obj);
                    vm.isPercentage=true;
                    vm.CheckPercentageOrNumber=1;
                }
                else{
                    vm.setSlider();
                }
                vm.series[1].data.push(mailChimpDataObj.READ_COUNT);
                vm.series[0].data.push(mailChimpDataObj.DELIVERED_COUNT);
                vm.series[2].data.push(mailChimpDataObj.CLICKED_COUNT);
                vm.series[3].data.push(mailChimpDataObj.SOFT_BOUNCED_COUNT);
                vm.series[4].data.push(mailChimpDataObj.HARD_BOUNCED_COUNT);
                vm.series[5].data.push(mailChimpDataObj.CLICKED_COUNT);
                vm.series[6].data.push(mailChimpDataObj.UNSUBSCRIBED_COUNT);
                vm.chartOptions = {
                    toolbar: 
                    {
                        show: false
                    },
                    xaxis: {
                        categories: getDates,
                        title: {
                            text: ''
                        }
                    },
                    yaxis: {
                        title: {
                            text: ''
                        },
                    },
                    dataLabels: {
                        enabled: true,
                    },
                    stroke: {
                        curve: 'smooth'
                    },
                }
           
             })
        }
    }
}
</script>