
    
<template>
    <section>
  <loader :is-visible="isLoading"></loader>
<TgList IsShowAction="true"
        ModuleName="CRM" 
        SubModuleCode="CRM_CAMPAIGNS" 
        IdentityColumn="campaign_id"
        :HeaderText="$t('Campaign')"
        :ListData="CampaignData" 
        :HeaderData="Headers"
        :HeaderButtons="listheaderbuttons" 
        :TotalRecords="TotalRecords"
        :SearchFields="leftSearchFields"
        :RenderRowActionMethod="renderActions"
        :ListDataCallBackFunction="FetchData"
        :LegendArray="LegendArray"
        :IsFilterApplied="searchCondition?true:false"
        :SortExp="SortExp"
        :SortBy="SortBy"
        :CurrentPageNumber="PageNumber"
        v-on:LeftsearchButtonClick="leftCommonSearch"
        v-on:PagerButtonClick="pagerMethod"
        v-on:HeaderButtonClick="commonHeaderButtonClick"
        v-on:ActionButtonClick="actionButtonClick"
        v-on:SortdataButtonClick="sortdata"
        v-on:CampaignTypeDDL="CampaignTypeDDL">
        <template v-slot:slotdata="{data}">
            <template v-if="data.column.COLUMN_NAME == 'status_id'">
                <listing-status  :isfor="'CRM_CAMPAIGNS'" v-if="isCRMShowStausDrop(data.row.status_id, 'CRM_CAMPAIGNS')" 
                ref="updatestatus" v-bind:dataid="data.row.campaign_id" 
                data-controller="Crm" v-bind:statuslist="StatusListForListing" v-bind:item="data" data-action="index" v-bind:dataselected="data.row.status_id" v-bind:callbackfunction="FetchData" v-bind:isselectedvaluestring="true">
                </listing-status>
                <a class="inactive-disabled" v-if="!isCRMShowStausDrop(data.row.status_id, 'CRM_CAMPAIGNS')">
                    <span v-bind:class="getStatusClass(data.row.status_id, 'status_id')">
                        {{data.row[data.column.COLUMN_NAME]}}
                    </span>
                </a>
            </template> 
            <template v-if="data.column.COLUMN_NAME == 'campaign_type'">
                <span>
                    <span v-bind:class="getCampaignIcon(data.row[data.column.COLUMN_NAME])"></span>
                    {{data.row[data.column.COLUMN_NAME]}}
                </span>
            </template>
        </template>
    </TgList>
    <searchfilter v-if="showFilter" submodulecode="CRM_CAMPAIGNS" modulename="CRM" v-on:close="showFilter = false" v-bind:callbackfunction="FetchData" v-on:FilterData="FilterData">
        <h5 slot="header">{{$t('Search')}}</h5>
    </searchfilter>
    <modalfollowupslist :srcId="souceID"  v-if="showAddEventModal" 
        modulename="CRM" submodulecode="CRM_CAMPAIGNS" :userid="userid" 
        @close="closeEventViewModal">
    </modalfollowupslist>
    <viewlayout v-if="ShowManageView" v-show="ShowManageView" submodulecode="CRM_CAMPAIGNS" modulename="CRM"
        @close="ShowManageView = false" v-bind:callbackfunction="FetchData" 
        :usertype="usertype"></viewlayout>
         <modalManageCampaign v-if="ShowManageCampaignPopup" :reqForm="reqForm" :campaignId="CampaignId" :campaignType="CampaignType" :reqType="reqType"  @close="ShowManageCampaignPopup = false"></modalManageCampaign>
         <modalManageSchedule v-if="isShowManageSchedule" :campaignId="CampaignId" :campaignName="CampaignName" :campaignType="CampaignType" :mailChimpTemplateId="MailChimpTemplateId" @close="isShowManageSchedule=false"></modalManageSchedule>
         <openAssignOwnerPopup v-if="showAssignOwnerPopup" :campaignId="CampaignId" @close="showAssignOwnerPopup=false"></openAssignOwnerPopup>
         <modalEmailStatistics v-if="ShowEmailStatisticsPopup" :campaignId="CampaignId" :EmailCampaignData="EmailCampaignData" @close="ShowEmailStatisticsPopup = false"></modalEmailStatistics>
         <modalManageSMSSchedule v-if="ShowManageSMSSchedule" :campaignId="CampaignId" :campaignType="CampaignType" @close="ShowManageSMSSchedule=false"></modalManageSMSSchedule>
</section>
</template>
    
<script>
import Schema from "./CampaignListingSchema";
import CampaignDataServices from "../../../services/DataService";
import searchfilter from '../../../components/Common/Modal/search-filter.vue'
import viewlayout from '../../../components/Common/Modal/view-layout.vue'
import modalManageCampaign from "../../../components/Common/Modal/modalManageCampaign.vue";
import modalfollowupslist from '../../../components/Common/Modal/modalfollowupslist.vue'
import TgList from '../../../components/Common/TgList.vue';
import modalManageSchedule from "../../../components/Common/Modal/modalManageSchedule.vue"
import openAssignOwnerPopup from '../../../components/Common/Modal/modalAssignCampaignOwner.vue'
import modalEmailStatistics from "../../../components/Common/Modal/modalEmailStatistics.vue";
import modalManageSMSSchedule from "../../../components/Common/Modal/modalManageSMSSchedule.vue";

export default {
    components: {
    TgList,
    searchfilter,
    viewlayout,
    modalfollowupslist,
    modalManageCampaign,
    modalManageSchedule,
    openAssignOwnerPopup,
    modalEmailStatistics,
    modalManageSMSSchedule
},
    data() {
        return {
            isLoading: false, 
            leftSearchFields: Schema.leftsearchSchema,
            listheaderbuttons: Schema.buttons,
            listActions: Schema.listActions,
            CampaignData: null,
            StatusListForListing: [],
            Headers: null,
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',
            PageSizeOption: null,
            CurrentPage: 1,
            TotalPages: 0,
            GroupStart: 1,
            GroupEnd: 3,
            Status: '',
            ModuleType: '',
            txtcampaign: '',
            selectedCampaignOwner: [],
            CampaignOwnerList: null,
            souceID: 0,
            showAddEventModal: false,
            campaignName: '',
            StatusIds: '',
            campaignOwner: '',
            campaignType: '',
            campaignDateFrom: '',
            campaignDateTo: '',
            langCode: 'en',
            showAddEmailTemplate: false,
            noRecord: false,
            showFilter: false,
            searchCondition: '',
            IsAnySearch: false,
            ShowManageView: false,
            ShowManageCampaignPopup:false,
            userid: null,
            usertype: null,
            companyId: null,
            privilegeParams: null,
            LegendArray: [],
            appliedFilter:[],
            isShowManageSchedule: false,
            CampaignId:0,
            MailChimpTemplateId:0,
            CampaignName:'',
            reqType:'Save',
            showAssignOwnerPopup:false,
            ShowEmailStatisticsPopup:false,
            ShowManageSMSSchedule: false,
            CampaignType:'email',
            reqForm:''
        }
    },
    created:async function () {
        var ths = this;
        var reqForm=ths.$route.params.reqForm;
            if(reqForm=='AddPost'){
                ths.ShowManageCampaignPopup=true;
                ths.reqForm='socialMedia';
            }
        this.userid = this.GetUserInfo.ID;
        this.usertype = this.GetUserInfo.UserType;
        this.companyId = this.GetUserInfo.CompanyId;
        this.privilegeParams = `controller=CRM&action=`;
        ths.getStatuslistForListing("CRM", "en");
        await Schema.bindleftCommonSearchdropdown(ths, "CRM", "CAMPAIGN_OWNER_SEARCH");
        await Schema.bindleftCommonSearchdropdown(ths, "CRM", "CRM_CAMPAIGN_TYPES");
        await Schema.bindleftCommonSearchdropdown(ths, "CRM", "LEAD_DEAL_STATUS");
        ths.FetchData();
        ths.LegendArray = [
                {
                    class: 'bg-primary colorboxsquare',
                    text: (ths.$t('CloseLeadLegend', ['Campaign']))
                }
            ]
    },

    methods: {
        renderActions: function(row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let  action = {...actionItem};
                switch (action.title) {
                    case 'View':
                    action.isVisible = true;
                    break;
                    case 'Edit':
                    case 'FollowUps':
                    action.isVisible = row.status_id != 'Closed';
                    break;
                    default: 
                    action.isVisible = true;
                    break;
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        clickEventOfColumn:function(row, column){
            if(column.COLUMN_NAME == 'campaign_name'){
                this.$router.push({ path: '/CRM/ViewCampaignDetails/'+row.campaign_id+'/CRM/CRM_CAMPAIGNS' });
            }
        },
        getStatuslistForListing: function (StatusType, lang) {
            var vm = this;
            var url = "langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";
            CampaignDataServices.statusdropdown(url).then(function (response) {
                if (response.data.status == "Success") {
                    var json = response.data.result;
                    vm.StatusListForListing = json.data;
                    vm.StatusIds=json.data[1].STATUS_ID;
                }
            });
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "downloadExceldata":
                    this.FetchData(true);
                    break;
                case "openFilterPopUp":
                    this.openFilterPopUp();
                    break;
                case "ManageView":
                    this.ManageView();
                    break;
                case "OpenManageCampaignPopup":
                    this.OpenManageCampaignPopup(0);
                break;
                    case "ManageSMSSchedule":
                    this.ManageSMSSchedule();
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "removeFilter":
                    var ths = this;
                    this.searchCondition = '';
                    const removeFilterindex= ths.listheaderbuttons.findIndex(object => {
                    return object.callbackfunction === 'removeFilter';});
                    ths.listheaderbuttons[removeFilterindex].isvisible=false;
                    const openFilterPopUpindex= ths.listheaderbuttons.findIndex(object => {
                    return object.callbackfunction === 'openFilterPopUp';});
                    ths.listheaderbuttons[openFilterPopUpindex].iconClass='fa fa-filter';
                     try {
                        ths.appliedFilter = [];
                     } catch (e) { };
                     ths.FetchData();
                    break;
                    case "setTourGuideSteps":
                    this.setTourGuideSteps(0);
                        break;
            }
        },
        actionButtonClick: function (callbackfunction, item, index, event) {
            var vm = this;
            switch (callbackfunction) {
                case 'checkPrivilegeView':
                    break;
                case 'checkPrivilegeDetails':
                    break;
                case 'openNoteListPopUp':
                    vm.openNoteListPopUp(item, index);
                    break;
                case 'openAddNewEvent':
                    vm.openAddNewEvent(item.campaign_id, item);
                    break;
                case 'OpenManageCampaignPopup':
                    vm.OpenManageCampaignPopup(item.campaign_id, item)   
                    break;
                case 'openAssignOwnerPopup':
                    vm.openAssignOwnerPopup(item.campaign_id)   
                    break;
                case 'Delete':
                    vm.commonDeleteData(item.campaign_id);
                    break;  
                case 'CloneCampaign':
                    vm.CloneCampaign(item);
                    break;
                case 'Archive':
                    vm.ArchiveCampaign(item.campaign_id);
                    break;
                case 'OpenEmailStatisticsPopup':
                    vm.OpenEmailStatisticsPopup(item);
                    break;
            }
        },
        OpenEmailStatisticsPopup: function (item) {
            this.EmailCampaignData = item;
            this.CampaignId= item.campaign_id;
            this.ShowEmailStatisticsPopup = true;
        },
        CloseEmailStatisticsPopup: function () {
            this.ShowEmailStatisticsPopup = false;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        openFilterPopUp: function (source_id) {
            this.cancelOrRefresh();
            this.showFilter = true;
        },
        cancelOrRefresh: function () {
            this.clientemail = "";
            this.selectedLeadOwner = "";
            this.NameSearch = "";
            this.CompanyName = "";
            this.hdnOwnerId = "";
            this.conditionForInstantSearch = "";
            this.IsAnySearch = false;
            this.FetchData();
        }, 
        ManageSMSSchedule: function(){
            this.ShowManageSMSSchedule = true;
        },   
        closeManageSMSSchedule: function(){
            this.ShowManageSMSSchedule = false;
        },
        ManageSchedule: function(){
            this.isShowManageSchedule = true;
        },
        closeManageCampaign: function (){
            this.isShowManageSchedule = false;
        }, 
        commonDeleteData: function (campaign_id) {
            var vm = this;
            CampaignDataServices.CheckPrivilege(vm.privilegeParams + 'Delete')
                .then(function (response) {
                    if (response.data) {
                        var val = "";
                        if(typeof(campaign_id) != 'undefined'){
                            val=campaign_id.toString();
                        }
                        else{
                            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                                if (val.length > 0)
                                    val += ',';
                                val += $(this).val();
                            });
                        }
                        if (val.length > 0) {
                            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Campaign")), function () {
                                var postJSON = {
                                    ids: val,
                                    moduleName: "CRM",
                                    subModuleCode: "CRM_CAMPAIGNS"
                                };
                                var objectPostString = JSON.stringify(postJSON);
                                CampaignDataServices.CommonDeleteData(objectPostString).then(function (response) {
                                    var box = "";
                                    if (response.status == 200) {
                                        $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                                         $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                                        $("#DeleteMultiple").addClass("disabled");
                                        box = vm.ShowAlert(vm.$t('DeletedSuccessfully', [vm.$t('Campaign')]), "success", true, vm.$t("Alert"));
                                        vm.PageNumber = parseInt(vm.PageNumber) - 1;
                                        if (vm.PageNumber <= 0) {
                                            vm.PageNumber = 1;
                                        }
                                        vm.FetchData();
                                    }
                                    else {
                                        box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Campaign')]), "failure", true, vm.$t("Alert"));
                                    }
                                    setTimeout(function () {
                                        box.modal('hide');
                                    }, 2000);

                                });
                            });
                        }
                        else {
                            vm.ShowAlert(vm.$t('Selectanyrecordtodelete'), "warning", true, vm.$t("Alert"));
                        }
                    }
                    else {
                        vm.ShowAlert('You are not authorized', "failure", true, "Alert");
                    }

    });
        },
        openAddNewEvent(item, source_id) {
            var vm = this;
            vm.souceID = source_id.campaign_id;
            vm.selectedItem = item;
            vm.showAddEventModal = (vm.showAddEventModal) ? false : true;
        },
        closeEventViewModal: function () {
            this.showAddEventModal = false;
        },
        ManageView: function () {
            var vm = this;
            this.ShowManageView = true;
            setTimeout(function () {
                $("#modalManageView").modal('show');
                setTimeout(function(){vm.SetBootstraptoolTip()}, 200);
            }, 1000);
        },
        OpenManageCampaignPopup: function (campaign_id, item) {
            var vm = this;
            vm.CampaignId=campaign_id;
            if(item){
                vm.CampaignType=item.campaign_type.toLowerCase();
            }
            if(campaign_id > 0){
                vm.reqType='Update';
            }else{
                vm.reqType='Save';
            }
            vm.ShowManageCampaignPopup = true;
        },
        openAssignOwnerPopup(campaign_id){
            var vm =this;
            vm.CampaignId=campaign_id;
            vm.showAssignOwnerPopup=true;
        },
        CloseManageCampaignPopup: function () {
            this.ShowManageCampaignPopup = false;
        },
        sortdata: function (s) {
          var SortDir = "sort tb_headerSortDown";
           this.SortBy = s;
                if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else
                {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                }
               
                this.FetchData();
                $("#" + s+'  span:first-child').attr("class", SortDir);
        },
        getRequestParams: function (pageNum, pageSize, sortBy, sortExp, isPaged, searchcondition) {
            let params = {};
            if (pageNum) { params["pageNum"] = pageNum; }
            if (searchcondition) { params["searchcondition"] = searchcondition; }
            if (isPaged) { params["isPaged"] = isPaged; }
            if (sortBy) { params["sortBy"] = sortBy; }
            if (sortExp) { params["sortExp"] = sortExp; }
            if (pageSize) { params["pageSize"] = pageSize; }
            return params;
        },       
        FilterData: function (pageNum, pageSize, sortBy, sortExp, searchcondition, filterItemData) {
            this.pageNum = pageNum;
            this.pageSize = pageSize;
            this.sortBy = sortBy;
            this.sortExp = sortExp;
            this.searchCondition = searchcondition;
            if (typeof filterItemData != null)
                this.appliedFilter.push(filterItemData);
            this.FetchData();
        },        
        FetchDataOld: function (downloadExceldata) {
            const params = this.getRequestParams(this.pageNum, this.pageSize, this.sortBy, this.sortExp, this.isPaged, this.searchCondition);
            var vueObj = this;
            vueObj.isLoading = true;
            if(downloadExceldata){
                params.pageSize = vueObj.TotalRecords;
            }else{
                params.pageSize = vueObj.PageSize;
            }
            params.pageNumber = vueObj.PageNumber;
            params.langCode = vueObj.langCode;
            params.campaignType=vueObj.campaignType;
            params.searchCondition = vueObj.searchCondition;
            if (vueObj.PageNumber) { params.pageNum = vueObj.PageNumber; }
            else { vueObj.PageNumber = params.pageNum = 1; }
            params.sortBy = vueObj.SortBy;
            params.sortExp = vueObj.SortExp;
            var srchCondition = "";
            if (!vueObj.isBlank(vueObj.searchCondition)) { srchCondition = srchCondition + vueObj.searchCondition; }
            if (!vueObj.isBlank(vueObj.conditionForInstantSearch)) {
                srchCondition = srchCondition + ((!vueObj.isBlank(vueObj.searchCondition)) ? (" AND " + vueObj.conditionForInstantSearch) : vueObj.conditionForInstantSearch);
            }
            if (srchCondition != "") {
                params.searchCondition = srchCondition; 
                if(vueObj.appliedFilter.length>0){
                const removeFilterindex= vueObj.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'removeFilter';
                });          
                vueObj.listheaderbuttons[removeFilterindex].isvisible=true;      
                const openFilterPopUpindex= vueObj.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'openFilterPopUp';
                });
                vueObj.listheaderbuttons[openFilterPopUpindex].iconClass='fa fa-filter text-success';
            }
            }
            else{
                if(vueObj.appliedFilter.length==0){
                const removeFilterindex= vueObj.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'removeFilter';
                });          
                vueObj.listheaderbuttons[removeFilterindex].isvisible=false;      
                const openFilterPopUpindex= vueObj.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'openFilterPopUp';
                });
                vueObj.listheaderbuttons[openFilterPopUpindex].iconClass='fa fa-filter';
            }
        }
            if (downloadExceldata) {
                params.isPaged = 0;
                CampaignDataServices.CampaignListing(params).then(function (response) {
                    vueObj.isLoading = false;
                    if (response.data != "") {
                        vueObj.JSONToCSVConvertor(response.data, "Campaign-Detail", true);
                    }
                    else {
                        vueObj.ModelnoRecord = true;
                    }
                    vueObj.isLoading = false;
                });
            }
            else {
                CampaignDataServices.CampaignListing(params).then(function (response) {
                    vueObj.isLoading = false;
                    if (response.data != null) {
                        vueObj.Headers = response.data.schema;
                        vueObj.Headers.forEach((column) => {
                                    column.settings = null;
                                    if(column.COLUMN_NAME == 'campaign_name'){
                                        column.settings = {
                                            clickEvent: vueObj.clickEventOfColumn,
                                            formatter: vueObj.columnDataFormatter
                                        }
                                    }
                                    if(column.COLUMN_NAME == 'status_id'){
                                        column.settings = {
                                            isInSlot : true
                                        }
                                    }
                                    if(column.COLUMN_NAME == 'campaign_type'){
                                        column.settings = {
                                            isInSlot : true
                                        }
                                    }
                                });
                        if (response.data.data.length > 0) {
                            vueObj.CampaignData = response.data.data;
                            vueObj.CampaignData.forEach((row)=>{
                                row.isCheckBoxDisabled=false;
                                row.additionalClass='';
                                      if(row.status_id=='Closed'){
                                        row.isCheckBoxDisabled=true;
                                        row.additionalClass='medium-bar'
                                         }   
                            row.rowDetailsUrl='';
                            if(row.rowDetailsUrl=='')
                            {
                                row.rowDetailsUrl='/CRM/ViewCampaignDetails/'+row.campaign_id+'/CRM/CRM_CAMPAIGNS';
                            }
                            });
                            vueObj.TotalRecords = response.data.data[0].total_records;
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            $('#DeleteCampaign').addClass('disabled');
                            $('#Sendbulkmail').addClass('disabled');
                            vueObj.noRecord = false;
                        } else {
                            vueObj.noRecord = true;
                            vueObj.CampaignData = [];
                            vueObj.TotalRecords = 0
                        }
                    }
                    else {
                        vueObj.noRecord = true;
                        vueObj.CampaignData = [];
                        vueObj.Headers = [];
                        vueObj.TotalRecords = 0
                    }
                    setTimeout(function(){
                                vueObj.ResponsiveDataTable('tablelistingdata')
                            }, 500);
                     vueObj.isLoading = false;
                });


            }
        },
        FetchData: function (downloadExceldata) {  //Use for new campaign listing for social media, sms and email campaign.
            const params = this.getRequestParams(this.pageNum, this.pageSize, this.sortBy, this.sortExp, this.isPaged, this.searchCondition);
            var vueObj = this;
            vueObj.isLoading = true;
            if(downloadExceldata){
                params.pageSize = vueObj.TotalRecords;
            }else{
                params.pageSize = vueObj.PageSize;
            }
            params.pageNumber = vueObj.PageNumber;
            params.langCode = vueObj.langCode;
            params.campaignType=vueObj.campaignType;
            params.searchCondition = vueObj.searchCondition;
            if (vueObj.PageNumber) { params.pageNum = vueObj.PageNumber; }
            else { vueObj.PageNumber = params.pageNum = 1; }
            params.sortBy = vueObj.SortBy;
            params.sortExp = vueObj.SortExp;
            var srchCondition = "";
            if (!vueObj.isBlank(vueObj.searchCondition)) { srchCondition = srchCondition + vueObj.searchCondition; }
            if (!vueObj.isBlank(vueObj.conditionForInstantSearch)) {
                srchCondition = srchCondition + ((!vueObj.isBlank(vueObj.searchCondition)) ? (" AND " + vueObj.conditionForInstantSearch) : vueObj.conditionForInstantSearch);
            }
            if (srchCondition != "") {
                params.searchCondition = srchCondition; 
                if(vueObj.appliedFilter.length>0){
                const removeFilterindex= vueObj.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'removeFilter';
                });          
                vueObj.listheaderbuttons[removeFilterindex].isvisible=true;      
                const openFilterPopUpindex= vueObj.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'openFilterPopUp';
                });
                vueObj.listheaderbuttons[openFilterPopUpindex].iconClass='fa fa-filter text-success';
            }
            }
            else{
                if(vueObj.appliedFilter.length==0){
                const removeFilterindex= vueObj.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'removeFilter';
                });          
                vueObj.listheaderbuttons[removeFilterindex].isvisible=false;      
                const openFilterPopUpindex= vueObj.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'openFilterPopUp';
                });
                vueObj.listheaderbuttons[openFilterPopUpindex].iconClass='fa fa-filter';
            }
        }
            if (downloadExceldata) {
                params.isPaged = 0;
                CampaignDataServices.CampaignListingV1(params).then(function (response) {
                    vueObj.isLoading = false;
                    if (response.data != "") {
                        vueObj.JSONToCSVConvertor(response.data, "Campaign-Detail", true);
                    }
                    else {
                        vueObj.ModelnoRecord = true;
                    }
                    vueObj.isLoading = false;
                });
            }
            else {
                CampaignDataServices.CampaignListingV1(params).then(function (response) {
                    vueObj.isLoading = false;
                    if (response.data != null) {
                        vueObj.Headers = response.data.schema;
                        vueObj.Headers.forEach((column) => {
                                    column.settings = null;
                                    if(column.COLUMN_NAME == 'campaign_name'){
                                        column.settings = {
                                            clickEvent: vueObj.clickEventOfColumn,
                                            formatter: vueObj.columnDataFormatter
                                        }
                                    }
                                    if(column.COLUMN_NAME == 'status_id'){
                                        column.settings = {
                                            isInSlot : true
                                        }
                                    }
                                    if(column.COLUMN_NAME == 'campaign_type'){
                                        column.settings = {
                                            isInSlot : true
                                        }
                                    }
                                });
                        if (response.data.data.length > 0) {
                            vueObj.CampaignData = response.data.data;
                            vueObj.CampaignData.forEach((row)=>{
                                row.isCheckBoxDisabled=false;
                                row.additionalClass='';
                                      if(row.status_id=='Closed'){
                                        row.isCheckBoxDisabled=true;
                                        row.additionalClass='medium-bar'
                                         }   
                            row.rowDetailsUrl='';
                            if(row.rowDetailsUrl=='')
                            {
                                row.rowDetailsUrl='/CRM/ViewCampaignDetails/'+row.campaign_id+'/CRM/CRM_CAMPAIGNS';
                            }
                            });
                            vueObj.TotalRecords = response.data.data[0].total_records;
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            $('#DeleteCampaign').addClass('disabled');
                            $('#Sendbulkmail').addClass('disabled');
                            vueObj.noRecord = false;
                        } else {
                            vueObj.noRecord = true;
                            vueObj.CampaignData = [];
                            vueObj.TotalRecords = 0
                        }
                    }
                    else {
                        vueObj.noRecord = true;
                        vueObj.CampaignData = [];
                        vueObj.Headers = [];
                        vueObj.TotalRecords = 0
                    }
                    setTimeout(function(){
                                vueObj.ResponsiveDataTable('tablelistingdata')
                            }, 500);
                     vueObj.isLoading = false;
                });


            }
        },
        async CloneCampaign(item){
            var vm =this;
            vm.CampaignId=item.campaign_id;
            vm.reqType='Clone';
            vm.ShowManageCampaignPopup = true;
        //     vm.confirmR(vm.$t("CloneConfirmMessage",[vm.$t('Campaign')]), true, (vm.$t("Campaign")), async function () {
        //     var url = `campaign_id=${item.campaign_id}`;
        //     await CampaignDataServices.CloneCampaign(url).then(async function(response){
        //         if(response.data !=null){
        //             if(response.data=='SUCCESS')
        //             {
        //                 vm.ShowAlert(vm.$t('CloneSuccess', [vm.$t('Campaign'),item.campaign_name]), "success", true, vm.$t("Alert"));
        //                 vm.FetchData();
        //             }
        //             else{
        //                 vm.ShowAlert(vm.$t('Something_Wrong'), "failure", true, vm.$t("Alert"));
        //             }
        //         }
        //     })
        // })
        },
        ArchiveCampaign: function (campaign_id) {
            var vm =this;
            vm.confirmR(vm.$t("ConfirmArchive",[vm.$t('Archive')]), true, (vm.$t("Campaign")), async function () { 
             var url = {
                        ID: campaign_id,
                        StatusId: vm.StatusIds,
                        IsFor: 'CRM_CAMPAIGNS'
                       }
            var objectPostString = JSON.stringify(url);
            await CampaignDataServices.CommonUpdateStatus(objectPostString).then(async function(response){
                if (response.data != null) {
                    if (response.data.status === 'Success') {
                        vm.ShowAlert(vm.$t("ArchiveSuccessfully"), "success",vm.$t('Alert'));
                        vm.PrevStatus = vm.StatusId;
                        if (typeof vm.callbackfunction != 'undefined' && vm.callbackfunction != null) {
                            vm.callbackfunction();
                        }
                    } else {
                        vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                    }
                } else {
                    vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                }
            })
            vm.FetchData();
            vueObj.isLoading = false;
        })
        },
        CampaignTypeDDL(e){
            var vm =this;
            vm.campaignType = e.target.value;
            vm.FetchData();
        },
        getCampaignIcon(campaign_name){
            var returnClass = '';
            if (campaign_name !== null) {
                var lowercaseCampaignName = campaign_name.toLowerCase();
            switch (lowercaseCampaignName) {
                case 'email' :
                    returnClass ='tablelist_icos email_icos';
                    break;
                case 'facebook':
                    returnClass ='tablelist_icos fb_icos';
                    break;
                case 'instagram':
                    returnClass ='tablelist_icos insta_icos';
                    break;
                case 'bing':
                    returnClass ='tablelist_icos bing_icos';
                    break;
                case 'twitter':
                    returnClass ='tablelist_icos tweet_icos';
                    break;
                case 'google':
                    returnClass ='tablelist_icos google_icos';
                    break;
                case 'sms':
                    returnClass ='tablelist_icos default_icos';
                    break;
                default:
                    returnClass;
            }
        }
            return returnClass;
        }
    },
    updated: function () {
        this.BindActionButtonEvent();
    }
}
</script>