<template lang="en">
<section>
    <div class="modal my-popups right fade show" role="dialog" style="display: block;">
        <loader :is-visible="isLoading"></loader>
        <div class="modal-dialog mw-1100" role="document">
            <div class="modal-content">
                <div class="modal-header mheader-user">
                    <button type="button" class="close" @click="closepopup"><i class="fa fa-times" aria-hidden="true"></i></button>
                    <h4 id="myModalLabel" class="modal-title">{{ $t("EmailStatistics") }}</h4>
                </div>
                <div class="w-100 float-left" style="margin-top:20px;">
                    <div class="listing col-12 p-0">
                        <div class="row">
                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-3 default-tag-vissible">
                                <div class="box border positionrelative float-left w-100">
                                    <div class="default-tags">
                                        <div class="defaultpic_control">
                                            <a title="" href="javascript:;" class="btn btn-view mr-2" @click="PreviewTemplate" data-toggle-tooltip="tooltip" data-original-title="Preview"><em class="fa fa-eye mr-2"></em>Preview</a>
                                            <div class="col-6" id="textcontent" :class="renderHtml" style="width:2000px;" v-html="EmailCampaignData.template_content"><em class="fa fa-eye mr-2"></em>Preview</div>
                                           
                                        </div>
                                    </div>
                                    <div class="img-ico text-center p-4 ticket-attachments">
                                        <img v-bind:src="thumbnailPath" />
                                    </div>
                                </div>
                            </div>
                            <div class="col-sm-12 col-md-6 col-lg-8 col-xl-9">
                                <div class="row">
                                    <div class="col-sm-12 col-md-6 col-lg-6">
                                        <div class="form-group"><label><b>Send Type: </b>{{EmailCampaignData.campaign_type}}</label></div>
                                    </div>
                                    <div class="col-sm-12 col-md-6 col-lg-6">
                                        <div class="form-group"><label><b>Send by:</b>{{EmailCampaignData.created_by}}</label></div>
                                    </div>
                                    <div class="col-sm-12 col-md-6 col-lg-6">
                                        <div class="form-group"><label><b>Created On:</b> {{$options.filters.formatDate(EmailCampaignData.created_at)}}</label></div>
                                    </div>
                                    <div class="col-sm-12 col-md-6 col-lg-6">
                                        <div class="form-group"><label><b>Attempted:</b> <em class="fa fa-users mr-2"></em>{{Attempted}} Contacts</label></div>
                                    </div>
                                    <div class="col-sm-12 col-md-6 col-lg-6">
                                        <div class="form-group"><label><b>Updated On:</b> {{$options.filters.formatDate(EmailCampaignData.modified_at)}}</label></div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="w-100 float-left">
                    <div class="card mb-3 probx">
                        <div class="card-header">
                            <h2>Subject: {{EmailCampaignData.campaign_name}}</h2>
                            <div class="cont_right">
                                <div class="bar"><em class="fa fa-calendar mr-2"></em>{{$options.filters.formatDateTime(EmailCampaignData.campaign_start_date)}}</div>
                            </div>
                        </div>
                        <div class="card-body">
                            <div class="listing col-12 p-0">
                                <div class="row">
                                    <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4 stat_slides mb-2">
                                        <div class="w-100 float-left p-4 d-flex border boxex">
                                            <div class="float-left w-80">
                                                <span class="float-left w-100 font-weight-medium">Attempted: {{Attempted}}</span>
                                                <span class="float-left w-100 value_ss">{{Attempted}}<span v-if="isPercentage">%</span></span>
                                            </div>
                                            <div class="float-right icobase">
                                                <i class="fa fa-file-text-o"></i>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4 stat_slides mb-2">
                                        <div class="w-100 float-left p-4 d-flex border boxex">
                                            <div class="float-left w-80">
                                                <span class="float-left w-100 font-weight-medium">Send: {{Send}}</span>
                                                <span class="float-left w-100 value_ss">{{Send}}<span v-if="isPercentage">%</span></span>
                                            </div>
                                            <div class="float-right icobase">
                                                <i class="fa fa-paper-plane-o"></i>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4 stat_slides mb-2">
                                        <div class="w-100 float-left p-4 d-flex border boxex">
                                            <div class="float-left w-80">
                                                <span class="float-left w-100 font-weight-medium">Skipped: {{Skipped}}</span>
                                                <span class="float-left w-100 value_ss">{{Skipped}}<span v-if="isPercentage">%</span></span>
                                            </div>
                                            <div class="float-right icobase">
                                                <i class="fa fa-history"></i>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                
                <div class="card-body" style="text-align: center;">
                <button @click="OpenCampaignStatisticsPopup">Load more Statistics</button>
                </div>
            </div>
        </div>
    </div>
    <ViewCampaignStatistics v-if="ShowCampaignStatisticsPopup" :campaignId="campaignId" :EmailCampaignData="EmailCampaignData" @close="ShowCampaignStatisticsPopup=false"></ViewCampaignStatistics>
    <TemplatePreview v-if="ShowTemplatePreview" :htmlContent="htmlContent" @closepopup="ShowTemplatePreview=false"></TemplatePreview>
</section>
</template>

<script>
import DataService from '../../../services/DataService';
import ViewCampaignStatistics from '../../../views/CRM/Campaign/ViewCampaignStatistics.vue';
import TemplatePreview from './TemplatePreview.vue';
import html2canvas from 'html2canvas';
export default {
    components: {
        ViewCampaignStatistics,
        TemplatePreview
    }, 
    props: {
        campaignId:  {
            type: Number,
            required: true
        },
        EmailCampaignData: {
            type: Object,
            required: true
        }
    },
    data() {
        return {
            isLoading:false,
            ShowTemplatePreview:false,
            ShowCampaignStatisticsPopup:false,
            Attempted:0,
            Send:0,
            Skipped:0,
            StatisticsData: [],
            isPercentage: false,
            startDate: new Date,
            endDate: new Date,
            htmlContent:'',
            thumbnailPath:'',
            imagePath:'' ,
            renderHtml:''          
        }
    },
    created() {
        this.GetCampaignStatistics();
        this.htmlContent=this.EmailCampaignData.template_content;
        setTimeout(() => {
        this.PreviewThumbnail();
    }, 2000);
    },
    methods: {
        PreviewThumbnail() {
            var vm = this;
            var element = document.querySelector("#textcontent");
            vm.renderHtml = 'd-none';
            html2canvas(element).then(function (canvas) {
                // Convert canvas to image
                vm.thumbnailPath = canvas.toDataURL("image/png");
            })
        },
        OpenCampaignStatisticsPopup: function (item) {
            this.CampaignId = item.campaign_id;
            this.ShowCampaignStatisticsPopup = true;
        },
        CloseCampaignStatisticsPopup: function () {
            this.ShowCampaignStatisticsPopup = false;
            this.ShowTemplatePreview=false;
        },
        closepopup() {
            var vm = this;
            vm.$parent.CloseEmailStatisticsPopup();
        },
        GetCampaignStatistics: function (){
            var vm = this;
            vm.isPercentage = true;
            var url = 'campaignid=CampaignId&dateform=&dateto=';
            DataService.GetCampaignStatistics(url).then(response=>{
                vm.StatisticsData= [];
                if(response.data != null){
                    vm.StatisticsData = response.data;
                    vm.Attempted= vm.StatisticsData[0].ATTEMPTED;
                    vm.Send = vm.StatisticsData[0].DELIVERED_COUNT;
                    vm.Skipped = vm.StatisticsData[0].SKIPPED;
                }
            })
        },
        PreviewTemplate:function(){
            var vm=this;
            vm.ShowTemplatePreview=true;
        }

        // GetCampaignStats: function(){
        //     var vm = this;
        //     var url = `campaignId=${vm.CampaignId}&startDate=${vm.$options.filters.formatDate(vm.startDate)}&endDate=${vm.$options.filters.formatDate(vm.endDate)}`;
        //     DataService.GetCampaignStats(url).then(response =>{
        //     }) 
        // }
        }
}
</script>
