<template>
    <div class="modal d-block my-popups dialog">
        <div class="modal-dialog">
            <div class="modal-content">
                <div class="modal-header">
                    <slot name="header">
                        <h5>{{$t('AddNewSMTP')}}</h5>
                    </slot>
                    <button type="button" title="" v-on:click="$emit('close')" class="close" data-original-title="Close">
                        <em class="fa fa-times" aria-hidden="true"></em>
                    </button>
                </div>
                <div class="modal-body">
                    <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                        <template v-slot:tgslot-Password="{ data }">
                            <div class="input-group">
                                <input type="password" :class="{'form-control w-100':true,'is-invalid':data.error.length !=''}" v-model="data.fieldInfo.value" :name="'Password'">
                                <span v-if="data.error.length !=''" name="Password" class="invalid-feedback">{{data.error}}</span>
                            </div>
                        </template> 
                    </dynamic-form>
                    <div class="col-lg-6">
                        <div class="py-2">
                            <small class="text-danger">{{ $t('MandatoryString') }}</small>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataService from "../../../services/DataService";
export default{
    props:{
        reqFrom:{
            type:String,
            required:true
        }
    },
    data(){
        return{
            FormSchema:[
                {
                    layoutType: "double",
                    Data:[
                        {
                            astype: "SelectField",
                            label: this.$t('SenderName'),
                            name: "SenderName",
                            value: "",
                            placeholder: "Select",                                                  
                            config: {
                                options: [{name:'Mail Chimp',value:'MailChimp'},{name:'Send Grid',value:'SendGrid'}],
                                onChange: this.onChangeSendThrough
                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('ProfileName'),
                            name: "ProfileName",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": false },
                        },
                        {
                            astype: "CheckBoxField", 
                            label: this.$t("EnableSSL"),                          
                            name: "EnableSSL",
                            value: '',
                            placeholder: "",
                            validationRules: { "required": false },           
                            config: {
                                options: [{name:'', value: true }]      
                            }
                        },
                        {
                            astype: "CheckBoxField", 
                            label: this.$t("EnableTSL"),                          
                            name: "EnableTSL",
                            value: '',
                            placeholder: "",
                            validationRules: { "required": false },           
                            config: {
                                options: [{name:'', value: true }]   
                            }
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Username'),
                            name: "UserName",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true },
                            visibility:true
                        },
                        {
                            astype: "SlotField",
                            label: this.$t('password'),
                            name: "Password",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": false },
                            visibility:true
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('AuthenticationType'),
                            name: "AuthenticationType",
                            value: "",
                            placeholder: "Select",
                            config: {
                                options: [{name:'Login',value:'Login'}]
                            },
                            validationRules: { "required": true },
                            visibility:true
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Domain'),
                            name: "Domain",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": false },
                            visibility:true,
                            config: {
                                options: []
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('AuthenticationPassword'),
                            name: "AuthenticationPassword",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true },
                            visibility:true,
                            config: {
                                options: []
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('AuthenticationEmail'),
                            name: "AuthenticationEmail",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true },
                            visibility:true,
                            config: {
                                options: []
                            },
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Save"
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.close
                },
            ]
        }
    },
    created(){
        var vm=this;
        if(vm.reqFrom=='MailChimp'){
            vm.FormSchema[0].Data[8].visibility=false;
            vm.FormSchema[0].Data[9].visibility=false;
        }
        else if(vm.reqFrom=='SendGrid'){
            vm.FormSchema[0].Data[4].visibility=false;
            vm.FormSchema[0].Data[5].visibility=false;
            vm.FormSchema[0].Data[6].visibility=false;
            vm.FormSchema[0].Data[7].visibility=false;
        }
    },
    methods:{
        onSubmit(formFeilds, val) {
            var vm = this;
            var jsonObj={
                from_name      :   formFeilds.SenderName,
                smtp_server    :   formFeilds.ProfileName,
                smtp_security  :   formFeilds.EnableSSL,
                send_through   :   formFeilds.EnableTSL,
                user_name      :   formFeilds.UserName,
                password       :   formFeilds.Password,
                smtp_type      :   formFeilds.AuthenticationType,
                smtp_port      :   formFeilds.Domain,
                from_email     :   formFeilds.AuthenticationEmail,
                password       :   formFeilds.AuthenticationPassword
            }
            var url = JSON.stringify(jsonObj);
            DataService.SaveEmailSetting(url).then(response => {
                if(response.data=='INSERTED'){
                    vm.ShowAlert(vm.$t('AddedSuccess',[vm.$t('AddNewSMTP')]),"success", true, vm.$t("Alert"));
                    // vm.$parent.FetchData();
                    vm.$emit('close');
                }else if(response.data=='UPDATED'){
                    vm.ShowAlert(vm.$t('UpdatedSuccess',[vm.$t('AddNewSMTP')]),"success", true, vm.$t("Alert"));
                    // vm.$parent.FetchData();
                    vm.$emit('close');
                }else{
                    vm.ShowAlert(vm.$t('AddedError',[vm.$t('AddNewSMTP')]),"danger", true, vm.$t("Alert"));
                }
            });
        },
        close:function(){
            var vm=this;
            vm.$emit('close');
        }
    }
}
</script>