<template>
     <div class="modal d-block my-popups dialog">
        <div class="modal-dialog">
            <div class="modal-content">
                <div class="modal-header">
                    <slot name="header">
                        <h5 class="modal-title">{{modalHeader}} Listing</h5>
                        <a title="Add Skill" href="javascript:;" class="p-action-btn text-white float-right" @click="openCredentialPopup(modalHeader)"><i class="fa fa-plus"></i><br><span>Add</span></a>
                    </slot>
                    <button type="button" @click="$emit('Close')" title="" class="close" data-original-title="Close"><em class="fa fa-times" aria-hidden="true"></em></button>
                </div>
            
            <div class="modal-body">
                <tg-list :IsShowAction="  false " ModuleName="CRM" SubModuleCode="CRM_CAMPAIGN"
                :RenderRowActionMethod=" renderActions " v-on:ActionButtonClick=" actionButtonClick "
                IdentityColumn="REF_PAGE_ID" :HeaderText=" $t('Campaign') " :listType=" ['List'] " :ListData=" CredentialList "
                :HeaderData=" Headers " :TotalRecords=" TotalRecords " :ListDataCallBackFunction="FetchRecord"
                :showCheckBox=" false " :SortExp=" SortExp " :SortBy=" SortBy " :NorecordfoundText=" $t('NoRecordfound') "
                v-on:LeftsearchButtonClick=" leftCommonSearch " v-on:PagerButtonClick=" pagerMethod "
                v-on:SortdataButtonClick=" sortdata ">
            </tg-list>
            </div>
        </div>
        </div>
        <modaladdsmtpVue v-if="showAddSmtpPopup" @close="close"></modaladdsmtpVue>
        <modalManageAdditionalSMTPVue v-if="showAddSmtpPopup" @close="close" :reqFrom="modalHeader"></modalManageAdditionalSMTPVue>
     </div>
</template>
<script>
import DataService from '../../../services/DataService';
import modaladdsmtpVue from './modaladdsmtp.vue';
import modalManageAdditionalSMTPVue from './modalManageAdditionalSMTP.vue';
export default{
    components:{
        modaladdsmtpVue,
        modalManageAdditionalSMTPVue
    },
    props:{
        modalHeader:  {
            type: String,
            required: true
        },
    },
    data(){
        return{
            showAddSmtpPopup:false,
            CredentialList:[],
            showAddMailChimpOrSendGridPopup:false,
            Headers: [
                {
                    "COLUMN_NAME": "smtp_server",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "SMTP",
                    "DISPLAY_ORDER": 1
                },
                {
                    "COLUMN_NAME": "user_name",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "User Name",
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "password",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Password",
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": "from_email",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Email",
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": "smtp_port",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "SMTP Port",
                    "DISPLAY_ORDER": 4
                },
                {
                    "COLUMN_NAME": "smtp_security",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "SMTP_Security",
                    "DISPLAY_ORDER": 5
                }
            ],
        }
    },
    created:function(){
        var vm=this;
        vm.FetchRecord();
    },
    methods:{
        renderActions: function (row) {
           var vm=this;
        },
        actionButtonClick(callbackfunction, item, index, event, isChild) {
            
        },
        leftCommonSearch() {

        },
        openCredentialPopup:function(dta){
            var vm=this;
            if(dta=='MailChimp' && dta=='SendGrid'){
                vm.showAddMailChimpOrSendGridPopup=true;
            }else{
                vm.showAddSmtpPopup=true;
            }
        },
        close:function(){
            var vm=this;
            vm.showAddSmtpPopup=false;
            vm.showAddMailChimpOrSendGridPopup=false;
        },
        ClosemodalManageAdditionalSMTP:function(){
            var vm=this;
            vm.showAddMailChimpOrSendGridPopup=false;
        },
        pagerMethod: function (value) {
            // this.PageSize = value.PageSize;
            // this.PageNumber = value.PageNumber;
            // this.FetchData();
        },
        FetchRecord:function(){
            var vm=this;
            var url = `profileId=&sourceType=${vm.modalHeader}`;
            DataService.GetEmailProfile(url).then(function (response) {
                vm.CredentialList=response.data;
            })
        }
    }
}
</script>
