<template>
    <section>
        <div class="modal my-popups  fade show" role="dialog" style="display: block;">
            <div class="modal-dialog mw-1100" role="document">
                <div class="modal-content">
                    <div class="modal-header mheader-user">
                        <button type="button" class="close" @click="$emit('close')"><i class="fa fa-times"
                                aria-hidden="true"></i></button>
                        <h4 id="myModalLabel" class="modal-title">{{ $t("AssignCampaignOwner") }}</h4>
                    </div>
                    <div class="modal-body">
                        <dynamic-form v-if="isLoaded" lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit" />
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import DataServices from "../../../services/DataService";
export default {
    props:{
        campaignId:  {
            type: Number,
            required: true
        },
    },
    data(){
        return{
            isLoaded:false,
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "CustomSelectField",
                            //mode: "tag",
                            label: this.$t('CampaignOwner'),
                            name: "CampaignOwner",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: "",
                            validationRules: { "required": true },
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save"
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.CloseAssignOwnerPopup
                },
            ],
        }
    },
    async created(){
        var vm = this;
        await vm.GetCampaignOwners();
        vm.isLoaded=true;
    },
    methods:{
        onSubmit: function (formfields, values) {
            var vm = this;
            var obj ={
                campaign_id: vm.campaignId ,
                campaign_owner_id: parseInt(formfields.CampaignOwner.value)
            }
            var url = JSON.stringify(obj);
            DataServices.AssignCampaignOwner(url).then(function(response){
                if(response.data !='' && response.data =='SUCCESS'){
                    vm.ShowAlert('Campaign Owner assigned successfully', "success", true, vm.$t("Alert"));
                    vm.$parent.FetchData();
                    vm.$emit('close');
                }
                else{
                    vm.ShowAlert(vm.$t("Something_Wrong"), "failure", true, vm.$t("Alert"))
                }
            })
        },
        CloseAssignOwnerPopup(){
            var vm =this;
            vm.$emit('close');
        },
        async GetCampaignOwners() {
            var vm = this;
            var url = "id=&moduleName=CRM&type=CRM_USERS";
            await DataServices.getDDLData(url + "&search=").then(response => {
                if (response.data != '') {
                    var CampaignOwnerData = response.data.DATA;
                    var campDDLData = [];
                    if (CampaignOwnerData != null) {
                        CampaignOwnerData.forEach(x => {
                            campDDLData.push({
                                name: x.name,
                                value: x.value
                            })
                        })
                        vm.FormSchema[0].Data[0].config.options = campDDLData;
                    }
                }
            });
        },
    }
}
</script>